#treatment-top {
  background: linear-gradient(rgba(0, 0, 0, 0.25), rgba(0, 0, 0, 0.25)),
    url("../../assets/images/zetong-li-k-e_Vj5ij9g-unsplash.jpg");
  background-position: center bottom;
  background-repeat: no-repeat; /* Do not repeat the image */
  background-size: cover;
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  margin: 0px;
  padding: 75px;
}

#treatment-top h2 {
  font-size: 44px;
  font-weight: lighter;
}

#treatment-top h3 {
  font-size: 30px;
  font-weight: lighter;
}

#treatment-top p {
  width: 50%;
  font-size: 20px;
}

#treatment-two {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  padding: 75px;
  background-color: rgb(42, 29, 59);
}

#treatment-two-text {
  width: 40%;
  padding: 50px;
  color: white;
}

#treatment-two-text h2 {
  font-size: 40px;
  font-weight: lighter;
}

#treatment-two-text p {
  font-size: 20px;
}

#treatment-two-text li {
  font-size: 18px;
  padding: 10px;
}

#treatment-two img {
  width: 50%;
  height: 50%;
}

#treatment-three {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  padding: 75px;
}

#treatment-three-text {
  width: 30%;
  padding: 50px;
  color: rgb(42, 29, 59);
}

#treatment-three-text h2 {
  font-size: 40px;
  font-weight: lighter;
}

#treatment-three-text p {
  font-size: 20px;
}

#treatment-three img {
  width: 60%;
  height: 60%;
}

#treatment-four {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  padding: 75px;
  background-color: rgb(42, 29, 59);
}

#treatment-four img {
  width: 50%;
  height: 50%;
}

#treatment-four-text {
  width: 40%;
  padding: 50px;
  color: white;
}

#treatment-four-text h2 {
  font-size: 40px;
  font-weight: lighter;
}

#treatment-four-text p {
  font-size: 20px;
}

#treatment-four-text li {
  font-size: 18px;
  padding: 10px;
}

/* Extra small devices */
@media only screen and (min-width: 320px) and (max-width: 479px) {
  #treatment-top p {
    width: 100%;
  }

  #treatment-two {
    padding: 60px;
  }

  #treatment-two img {
    display: none;
  }

  #treatment-two-text {
    width: 100%;
    padding: 0px;
  }

  #treatment-two-text h2 {
    font-size: 27px;
  }

  /* #treatment-two-text p {
        font-size: 15px;
    }

    #treatment-two-text li {
        font-size: 13px;
    } */

  #treatment-three img {
    display: none;
  }

  #treatment-three-text {
    width: 100%;
    padding: 0px;
  }

  #treatment-three-text h2 {
    font-size: 30px;
  }

  #treatment-four img {
    display: none;
  }

  #treatment-four-text {
    width: 100%;
    padding: 0px;
  }

  #treatment-four-text h2 {
    font-size: 30px;
  }
}

