    padding: 60px;
  }

  #treatment-two img {
    display: none;
  }

  #treatment-two-text {
    width: 100%;
    padding: 0px;
  }

  #treatment-two-text h2 {
    font-size: 27px;
  }

  /* #treatment-two-text p {
        font-size: 15px;
    }

    #treatment-two-text li {
        font-size: 13px;
    } */

  #treatment-three img {
    display: none;
  }

  #treatment-three-text {
    width: 100%;
