@media only screen and (min-width: 769px) and (max-width: 900px) {
  /* #treatment-top p {
        width: 100%;
    } */

  #treatment-two {
    padding: 60px;
  }

  #treatment-two img {
    width: 100%;
    height: 100%;
    padding-right: 25px;
  }

  #treatment-two-text {
    width: 75%;
    padding: 25px;
  }

  #treatment-two-text h2 {
    font-size: 27px;
  }

  #treatment-two-text p {
    font-size: 15px;
  }

  #treatment-two-text li {
    font-size: 13px;
  }

  #treatment-three img {
    width: 100%;
    height: 100%;
    padding-left: 25px;
  }

  #treatment-three-text {
    width: 75%;
    padding: 25px;
  }

  #treatment-three-text h2 {
    font-size: 30px;
  }

  #treatment-three-text p {
    font-size: 15px;
  }

  #treatment-four img {
